.content {
    display: flex;
}

.left-content {
    width: 70%;
    height: 100%;
    padding: 10px;
}

.left-content h3 a {
    text-decoration: none;
}
.right-content {
    width: 30%;
    padding: 10px;
    display: flex;
    flex-direction: column;
}

.right-content img {
    width: 100%;
}

hr {
    border: none;
    border-top: 1px dotted black;
}

h3 {
    margin-bottom: 0;
}

.article-top {
    display: flex;
    margin-bottom: 10px;
}

.article-info {
    display: flex;
}

.article-date {
    border-right: 1px solid #000;
    padding: 10px;
}

.artile-category {
    padding: 10px;
}

.article-text {
    margin-left: 10px;
}

.right-content {
    display: flex;
    flex-direction: column;
}